@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;700&display=swap');

*{
  font-family: 'Nunito', sans-serif;
}

:root{
  /* --global-primary-color:#ff452b; */
  --global-primary-color:#4630AB;
  --global-secondary-color:#2B0548;
  --global-text-color:#212121;
  --global-bg-color-soft:#E5E5E5;
  --global-border-radius:8px;
  --global-border: 1px solid #e9e9e9;
  --global-error-color:rgb(192, 62, 62);

  --global-bg-color:#E5E5E5;
}

.dark{
  --global-primary-color:#2B0548;
  --global-secondary-color:#4630AB;
  --global-text-color:#fafafa;
  --global-bg-color-soft:#212121;
  --global-border:.5px solid #3f3f3f;
  
  --global-bg-color:#181818;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--global-bg-color);
}

*,
*::after,
*::before{
  box-sizing: border-box;
}